<template>
  <div>
    <h3 class="title is-3 level-item">Abaixo estão seus pedidos</h3>
    <br>
    <div v-if="this.cli.clientForUserId[0]">
      <ApolloQuery
        :query="require('@/graphql/queries/pedidos.gql')"
        :variables="{user_envia_id:this.cli.clientForUserId[0].id}"
      >
        <template slot-scope="{ result: { data, loading },isLoading }">
          <div v-if="isLoading">Loading...</div>
          <div v-else>
            <div v-for="pedido of data.propostaEnvia" :key="pedido.id">
              <div class="column is-12-dektop is-full-mobile">
                <div class="card">
                  <div class="columns">
                    <div class="column is-3-desktop is-full-mobile">
                      <div class="column">
                        <div class="level-item">
                          <img
                            class="img"
                            width="80%"
                            height="10%"
                            :src="`http://graphql.me/imagem/${pedido.user_recebe.imagem}`"
                            alt="Placeholder image"
                          >
                        </div>
                        <div class="title is-5 level-item">
                          <div class="level-item">{{pedido.user_recebe.user.name}}</div>
                        </div>
                        <div>
                          <router-link
                            :to="`/page/cliente/categories/profissional/${pedido.user_recebe.id}`"
                          >
                            <button
                              class="btn-7 column is-12-desktop is-full-mobile"
                            >Ver Perfil
                            </button>
                          </router-link>
                        </div>
                      </div>
                    </div>
                    <div class="column is-9-desktop is-full-mobile">
                      <div class="columns">
                        <div class="column is-6-desktop">
                          <div class="column">
                            <div class="title is-3">{{pedido.titulo}}</div>
                          </div>
                        </div>
                        <div class="column is-offset-2">
                          <div class="column">
                            <div v-if="pedido.status===0" class="espera">Esperando resposta</div>
                            <div v-if="pedido.status===1" class="aceitou">Pedido aceito :)</div>
                            <div v-if="pedido.status===2" class="recusou">Pedido Recusado :(</div>
                            <div v-if="pedido.status===3" class="finalizou">Pedido Finalizado!</div>
                            <div v-if="pedido.status===4" class="finalizou">Usuário Avaliado!</div>
                          </div>
                        </div>
                      </div>
                      <div class="column is-9-desktop">
                        <div class="title is-5">
                          <b>Local do serviço:</b>
                          {{pedido.local}}
                        </div>
                      </div>
                      <div class="columns">
                        <div class="column is-6-desktop">
                          <div class="column">
                            <div class="title is-5">
                              <b>Valor:</b>
                              {{pedido.valor}}
                            </div>
                          </div>
                        </div>
                        <div class="column">
                          <div class="column">
                            <div class="title is-5">
                              <b>Tipo do serviço:</b>
                              {{pedido.tipo}}
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="column is-9-desktop">
                        <div class="title is-5" style="text-align:justify">
                          <b>Descrição do serviço:</b>
                          {{pedido.descricao}}
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="column is-12-desktop is-full-mobile">
                    <div class="column" v-if="pedido.status===0">
                      <div
                        class="title is-4 obsEspera"
                        style="text-align:justify"
                        v-if="pedido.status===0"
                      >OBS: Espere um pouco até o {{pedido.user_recebe.user.name}} poder analisar sua proposta!</div>
                    </div>
                    <div v-if="pedido.orcamento[0] && pedido.status === 1">
                      <!-- Se tiver orçamento para esse pedido e se ele foi aceito -->
                      <div class="columns">
                        <div class="column is-8-desktop">
                          <div
                            class="title is-4 obsAceita"
                            style="text-align:center"
                          >O (a) {{pedido.user_recebe.user.name}} aceitou sua proposta entre em contato com ele através do número: {{pedido.user_recebe.telefone}}</div>
                        </div>
                        <div class="column">
                          <router-link
                            :to="`/page/cliente/pedidos/orcamento/${pedido.orcamento[0].id}`"
                          >
                            <button
                              class="btn-15 column is-12-desktop is-full-mobile"
                            >Ver disponibilidade</button>
                          </router-link>
                        </div>
                      </div>
                    </div>
                    <div v-if="!pedido.orcamento[0] && pedido.status === 1">
                      <!-- Se não existir orçamento para o pedido faz isso-->
                      <div class="column">
                        <div
                          class="title is-4 obsEspera"
                          style="text-align:center"
                        >OBS: O pedido foi aceito, mas ele não enviou ainda sua disponibilidade! Contate ele pelo telefone {{pedido.user_recebe.telefone}} ou pelo email {{pedido.user_recebe.user.email}}</div>
                      </div>
                    </div>
                    <div class="column" v-if="pedido.status===2">
                      <div
                        class="title is-4 obsRecusa"
                        style="text-align:center"
                      >O(A) {{pedido.user_recebe.user.name}} infelizmente recusou seu pedido :(</div>
                    </div>
                    <div v-if="pedido.status === 3" class="column is-12-desktop is-full-mobile">
                      <div
                        class="title is-4 obsFinaliza"
                        style="text-align:center"
                      >Esse pedido foi finalizado com sucesso!</div>
                      <img
                        src="../../assets/5estrelas.png"
                        class="column is-2-desktop is-12-mobile is-offset-5"
                        alt="cover image"
                      >
                      <router-link :to="`/page/cliente/pedidos/avaliacao/${pedido.id}`">
                        <button
                          class="btn-15 column is-12-desktop is-full-mobile"
                        >Avaliar profissional</button>
                      </router-link>
                      <div class="column">
                        <router-link
                          :to="`/page/cliente/pedidos/orcamento/${pedido.orcamento[0].id}`"
                        >
                          <button
                            class="btn-15 column is-12-desktop is-full-mobile"
                          >Ver disponibilidade</button>
                        </router-link>
                      </div>
                    </div>
                    <div v-if="pedido.status === 4" class="column is-12-desktop is-full-mobile">
                      <img
                        src="../../assets/5estrelas.png"
                        class="column is-2-desktop is-12-mobile is-offset-5"
                        alt="cover image"
                      >
                      <div
                        class="title is-4 obsFinaliza"
                        style="text-align:center"
                      >Usuário foi avaliado para esse serviço veja o perfil dele!</div>
                    </div>
                  </div>
                  <br>
                </div>
                <!-- FIM DO CARD -->
              </div>
              <!-- Fim da <div class="column is-12-dektop is-full-mobile"> -->
            </div>
            <!-- FIM DO V-FOR -->
          </div>
        </template>
      </ApolloQuery>
    </div>
    <div v-else>
      <div class="title is-4 level-item">Voçê não se cadastrou ainda!</div>
    </div>
  </div>
</template>
<script>
import gql from "graphql-tag";
import cliForUserId from "@/graphql/queries/clientForUserId.gql";

export default {
  data() {
    return {
      me: null,
      cli: []
    };
  },
  apollo: {
    me: gql`
      query {
        me {
          id
          name
          email
          status
        }
      }
    `,
    cli: {
      query: cliForUserId,
      variables() {
        return {
          user_id: this.me.id
        };
      },
      update(data) {
        this.cli = data;
        return this.cli;
      }
    }
  }
};
</script>
<style>
@import "../../cssFiles/inputsAndButtons.css";

.img {
  border-radius: 100%;
}

.espera {
  background: yellow;
  text-transform: uppercase;
  padding: 0.2em 0.7em;
  font-weight: bold;
  text-align: center;
  border-radius: 10px;
  border: 2px solid black;
}
.aceitou {
  background: seagreen;
  text-transform: uppercase;
  padding: 0.2em 0.7em;
  font-weight: bold;
  text-align: center;
  border-radius: 10px;
  border: 2px solid black;
  color: white;
}

.recusou {
  background: red;
  text-transform: uppercase;
  padding: 0.2em 0.7em;
  font-weight: bold;
  text-align: center;
  border-radius: 10px;
  border: 2px solid black;
  color: white;
}

.finalizou {
  background: rgb(99, 74, 110);
  text-transform: uppercase;
  padding: 0.2em 0.7em;
  font-weight: bold;
  text-align: center;
  border-radius: 10px;
  border: 2px solid black;
  color: white;
}

.obsFinaliza {
  background: rgb(99, 74, 110);
  padding: 1em 2em;
  color: white;
}

.obsEspera {
  background: rgb(250, 250, 127);
  padding: 1em 2em;
}

.obsAceita {
  background: rgb(127, 219, 127);
  padding: 1em 2em;
}

.obsRecusa {
  background: rgb(226, 118, 118);
  padding: 1em 2em;
}
</style>